import * as React from "react";
import { graphql, StaticQuery } from "gatsby";
import Layout from "../templates/layout-page";
import Mdx from "../components/mdx";

/**
 * QUery
 */
const query = graphql`
  query MyQuery {
    mdx(slug: { eq: "legal" }) {
      body
    }
  }
`;

/**
 * View
 */
function IndexPage() {
  return (
    <StaticQuery
      query={query}
      render={(data) => (
        <Layout meta={{ title: "Mentions lÃ©gales" }}>
          <div className="container-reading my-5">
            <Mdx body={data.mdx.body} />
          </div>
        </Layout>
      )}
    />
  );
}

export default IndexPage;
